.about_container {
	display: grid;
	grid-template-columns: 35% 50%;
	gap: 15%;
}
.about_me {
	width: 100%;
	aspect-ratio: 1/1;
	border-radius: 2rem;
	/* background: linear-gradient(
		45deg,
		transparent,
		var(--color-primary),
		transparent
	); */
	display: grid;
	place-items: center;
	margin-top: 50px;
}
.about_me img {
	border-radius: 2rem;
	overflow: hidden;
	transform: rotate(-10deg);
	transition: var(--transition);
	height: 120%;
	margin-top: 30px;
}
.about_me img:hover {
	transform: rotate(0deg);
}
.about_cards {
	display: flex;
	flex-wrap: wrap;
	gap: 1.5rem;
	justify-content: center;
}
.about_card {
	background: var(--color-bg-variant);
	border: 1px solid transparent;
	border-radius: 2rem;
	padding: 2rem;
	text-align: center;
	transition: var(--transition);
	width: 200px;
}
.about_card:hover {
	background: transparent;
	border-color: var(--color-primary);
	cursor: default;
}
.about_icon {
	color: var(--color-primary);
	font-size: 1.4rem;
	margin-bottom: 1rem;
}
.about_card h5 {
	font-size: 0.9rem;
}
.about_card small {
	font-size: 0.7rem;
	color: var(--color-light);
}
.about_content p {
	margin: 2rem 0 2.6rem;
	color: var(--color-light);
}

@media screen and (max-width: 1148px) {
	.about_container {
		grid-template-columns: 1fr;
		gap: 0;
	}
	.about_me {
		width: 80%;
		margin: 2rem auto 4rem;
	}
	.about_content p {
		margin: 1rem 0 1.5rem;
	}
}
@media screen and (max-width: 600px) {
	.about_me {
		width: 85%;
		margin: 0 auto 3rem;
	}

	.about_content {
		text-align: center;
	}
	.about_conten p {
		margin: 1rem 0;
	}
}
