footer {
	background: var(--color-primary);
	padding: 3rem 0;
	text-align: center;
	font-size: 0.8rem;
	margin-top: 7rem;
}

footer a {
	color: var(--color-bg);
	font-weight: 800;
	margin-bottom: 2rem;
	display: inline-block;
}
.footer_logo {
	font-size: 2rem;
	font-weight: 500;
	margin-bottom: 2rem;
	display: inline-block;
}
.permalinks {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	gap: 2rem;
	margin: 0 auto 3rem;
}
.footer_socials {
	display: flex;
	justify-content: center;
	gap: 1rem;
	margin-bottom: 4rem;
}
.footer_socials a {
	background: var(--color-bg);
	color: var(--color-white);
	padding: 0.5rem;
	border-radius: 0.7rem;
	display: flex;
	border: 1px solid var(--color-bg);
	font-size: 1.5rem;
}
.footer_socials a:hover {
	transform: scale(0.9);
}
.footer_copyright {
	color: var(--color-bg);
	font-size: 0.8rem;
	margin-bottom: -3rem;
}
@media screen and (min-width: 1441px) {
	.footer_logo {
		font-size: 3rem;
	}
	footer a {
		font-size: 2rem;
	}
	.footer_socials {
		gap: 2rem;
	}
	.footer_socials a {
		font-size: 2rem;
	}
	.footer_copyright {
		font-size: 1rem;
	}
}
